import returns from '../common/returns.vue'
import uniLoadMore from '../../components/uni-load-more/uni-load-more.vue'
	export default{
		components:{
			returns,
			uniLoadMore
		},
		data(){
			return{
				title:'课程订单',
				top_show:1,
				mid_show:6,
				data:[],
				more: 'more',
				page: 1,
				loadRecord: true,
				
			}
		},
		methods:{
			Index(){
				this.more = 'loading'
				let data = {
					userid:this.$store.state.user.id,
					page:this.page,
					limit:10,
					type:this.mid_show,
					video_type:this.top_show
				}
				this.uni_request(data)
			},
			chiose(type){ //音频视频切换
				this.more = 'loading'
				this.top_show = type
				this.mid_show = 6
				this.loadRecord = true
				this.page = 1
				this.data.length = 0
				let data = {
					userid:this.$store.state.user.id,
					page:this.page,
					limit:10,
					type:this.mid_show,
					video_type:this.top_show
				}
				this.uni_request(data)
			},
			chiose_status(type){ //订单状态切换
				this.more = 'loading'
				this.mid_show = type
				this.loadRecord = true
				this.page = 1
				this.data.length = 0
				let data = {
					userid:this.$store.state.user.id,
					page:this.page,
					limit:10,
					type:this.mid_show,
					video_type:this.top_show
				}
				this.uni_request(data)
			},
			uni_request(data){
				this.service.entire(this,'post',this.APIconfig.api_root.subuser.v_orderList,data,function(self,res){
					let data = self.data
					data.push(...res.data.order_list)
					console.log(self.data);
					self.page += 1
					self.more = 'more'
					if (res.data.order_list.length < 10) {
						self.more = 'noMore'
						self.loadRecord = false
						return
					}
					
				})
			}
				
		},
		onLoad(e) {
			this.mid_show = e.status
			console.log(this.mid_show)
		},
		onShow() {
			this.data.length = 0
			this.page = 1
			this.Index()
		},
		onReachBottom() {
			if (this.loadRecord == false) return
			this.Index()
		},
	}
